#full-page {
  position: fixed;
  background: rgba(255, 255, 255, 0.6);
  inset: 0 0 0 0;
  display: flex;
  justify-content: center;
}

.modal {
  position: absolute;
  inset: 5% 20% 5% 20%;
  border-radius: 0.7rem;
  padding: 30px;

  background: white;
}

#btn-close-modal {
  position: absolute;
  right: -20px;
  top: -20px;
}
