.face-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.value-label {
  background: #149df2;
  position: absolute;
  bottom: -10px;
  font-size: 1rem;
  height: 20px;
}

.value-label {
  visibility: hidden;
}

.face-box:hover .value-label {
  visibility: visible;
}
